import comicobject from "../../../script/comicobject";

export default {
  name: "buy",
  mixins: [comicobject],
  data() {
    return {
      comic_list: undefined,
      comic_rows: undefined,
      ROW_COUNT: this._g.COMMON_ROW_COUNT,
    }
  },
  created() {
    this.load_comic_buy_list({
      start: 0,
      limit: 30,
      cb: this.on_comic_buy
    });
  },
  methods: {
    on_comic_buy: function (response) {
      console.log("response:", response)
      this.comic_list = response.comic_data_list;
      this.comic_rows = this.do_comic_list(this.comic_list, this.ROW_COUNT);
    }
  }
}
