import forumobjct from "../script/forumobjct";

export default {
  name: "list",
  mixins: [forumobjct],
  data() {
    return {
      fid: undefined,
      forum_conf:undefined
    }
  },
  created() {

    window._t = this;

    var urlvars = this.getUrlVars();
    console.log('this.$route.params.id:', this.$route.params.id);
    this.fid = this.$route.params.id;
    this.get_forum_threads({fid: this.fid, start: 0, limit: 36, cb: this.on_load_threads});

    this.forum_conf =  this.forum_optimization_conf[this.fid]

  }, methods: {
    on_load_threads: function (data) {
      console.log('on_load_threads:', data)
    }
  }
}
